<template>
  <div>
    <div class="pd-10-bottom">
      <h2 class="title">{{ internationalDict.joinUs }}</h2>
    </div>
    <div class="container">
      <el-tabs v-model="activeName">
        <el-tab-pane class="occupation-card-group">
          <span slot="label"><i class="el-icon-fork-spoon"></i> 餐飲前線</span>
          <div v-for="(item, index) of food" :key="index">
            <img :src="item.img" class="occupation-item" />
          </div>
        </el-tab-pane>
        <el-tab-pane class="occupation-card-group2">
          <span slot="label"><i class="el-icon-box"></i> 倉務</span>
          <el-card v-for="(item, index) of storage" :key="index">
            <div class="occupation-card">
              <div></div>
              <div class="occupation-info">
                <div class="occupation-info-detail">
                  <h3 class="fw-bold">
                    {{ item.name }}
                    <el-tag
                      class="occupation-tag"
                      :type="item.type === 'fullTime' ? 'primary' : 'warning'"
                      size="mini"
                      >{{ item.typeLabel }}</el-tag
                    >
                  </h3>
                  <div v-html="dutyFomatter(item.duty)"></div>
                  <p v-show="item.workDate">
                    <span class="label">工作天數:</span>{{ item.workDate }}
                  </p>
                  <p><span class="label">工作時間:</span>{{ item.workHour }}</p>
                  <p>
                    <span class="label">工作地點:</span>{{ item.workPlace }}
                  </p>
                  <p v-if="item.type === 'fullTime'">
                    <span class="label">月薪:</span>&#36;{{ item.salary }}
                  </p>
                  <p v-else>
                    <span class="label">時薪:</span>&#36;{{ item.salary }}
                  </p>
                </div>
              </div>
            </div>
          </el-card>
        </el-tab-pane>
        <el-tab-pane>
          <span slot="label"
            ><i class="el-icon-office-building"></i> 寫字樓</span
          >
          <div v-for="(item, index) of office" :key="index">
            <img :src="item.img" class="occupation-item" />
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>

    <div v-if="windowSize === 'big'" class="hotline-box">
      <div class="hotline-container">
        <h3>招聘專線</h3>
        <div>
          <p>WHATSAPP</p>
          <p>5441 3133</p>
          <p>線上初步面試</p>
        </div>
        <img src="@/assets/jobHotLine.png" class="hotline-QRcode" />
      </div>
    </div>
    <div v-else>
      <div class="hotline-box-moblie">
        <a href="https://api.whatsapp.com/send/?phone=85254413133&text">
            <el-button round type="primary">WhatsApp招聘專線</el-button>
        </a>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "join-us",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      activeName: "",
      food: [
        {
          img: require("@/assets/joinus/food1.jpg"),
        },
        {
          img: require("@/assets/joinus/food2.jpg"),
        },
        {
          img: require("@/assets/joinus/food3.jpg"),
        },
        {
          img: require("@/assets/joinus/food4.jpg"),
        },
        {
          img: require("@/assets/joinus/food5.jpg"),
        },
        {
          img: require("@/assets/joinus/food6.jpg"),
        },
      ],
      storage: [
        {
          type: "fullTime",
          typeLabel: "全職",
          name: "全職倉務助理(周休兩天)",
          salary: 12000,
          workPlace: "荃灣",
          duty: "處理一般倉務工作,包括|1.跟單執貨,點貨,出貨|2.倉務管理,每月工作盤點",
          workDate: "周休兩天",
          workHour: "09:00 - 18:00",
        },
        {
          type: "fullTime",
          typeLabel: "全職",
          name: "全職倉務助理(周休一天)",
          salary: 14500,
          workPlace: "荃灣",
          duty: "處理一般倉務工作,包括|1.跟單執貨,點貨,出貨|2.倉務管理,每月工作盤點",
          workDate: "周休一天",
          workHour: "09:00 - 18:00",
        },
        {
          type: "partTime",
          typeLabel: "兼職",
          name: "兼職倉務助理",
          salary: 65,
          duty: "處理一般倉務工作,包括|1.跟單執貨,點貨,出貨|2.倉務管理,每月工作盤點",
          workDate: "",
          workHour: "09:00 - 16:00",
        },
      ],
      office: [
        {
          img: require("@/assets/joinus/office1.jpg"),
        },
      ],
    };
  },
  computed: {
    internationalDict() {
      return this.$store.getters.getInternationalDict;
    },
    windowSize() {
      return this.$store.getters.getWindowSize;
    },
  },
  watch: {},
  mounted() {},
  methods: {
    dutyFomatter(context) {
      let res = '<span class="label">職責:</span>';
      let arr = context.split("|");
      res += `<span>${arr[0]}</span>`;
      if (arr.length < 2) {
        return res;
      }
      res += "<ul>";
      for (let i = 1; i < arr.length; i++) {
        res += `<li>${arr[i]}</li>`;
      }
      return (res += "</ul>");
    },
  },
};
</script>
<style>
.duty-list {
  list-style: none;
}
.fw-bold {
  color: var(--color-primary);
  font-weight: bold;
}
.label {
  font-weight: 700;
}
/* occupation */
.occupation-card-group {
  display: grid;
  gap: 1em;
  grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
}
.occupation-card {
  display: grid;
  gap: 1rem;
  grid-template-columns: min(10%, 5rem) 1fr;
}
.occupation-card-group2 {
  display: grid;
  gap: 1em;
  grid-template-columns: repeat(auto-fit, minmax(360px, 1fr));
}
.occupation-info {
  display: grid;
  grid-template-rows: min-content;
  gap: 5px;
}
.occupation-item {
  width: 100%;
  object-fit: fill;
}
.occupation-tag {
  padding-left: 2em;
}
.occupation-info-detail {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
}
/* hotline-box */
.hotline-box {
  position: fixed;
  width: 140px;
  bottom: 1em;
  right: 1em;
  border-radius: 10px;
  background-color: var(--color-primary);
  padding: 0.5em;
}
.hotline-box-moblie {
  position: fixed;
  bottom: 1em;
  right: 1em;
}
.hotline-container {
  display: flex;
  flex-direction: column;
  gap: 1em;
  color: aliceblue;
  justify-content: center;
  text-align: center;
}
.hotline-QRcode {
  transform: scale(0.8);
}
</style>
